@tailwind base;

@layer base {
  h1 {
    @apply text-[54px] font-black leading-[50px];
  }

  h2 {
    @apply text-[24px] font-normal leading-[20px] text-[#ff0a65] tracking-widest mt-2;
  }
}

@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: rgb(34, 4, 66);
  background: linear-gradient(
    0deg,
    rgba(34, 4, 66, 1) 0%,
    rgba(66, 6, 129, 1) 100%
  );
}

#root {
  height: 100%;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

canvas {
  touch-action: none;
}
